<template>
	<div id="home-page">
		<div class="home container">
			<h1 class="text-center">Hi, I'm Jhonarendra</h1>
			<p class="text-center mt-3" v-html="desc_animated"></p>
			<p class="text-center mt-4">
				<router-link to="/about" class="btn btn-rounded btn-outline btn-sm btn-white text-white m-2 pulse-anim" :disabled="is_countdown" tag="button">
					<span v-if="is_countdown">Explore {{countdown}}</span>
					<span v-else>Explore</span>
				</router-link>
				<!-- <router-link to="/about" class="btn btn-rounded btn-outline btn-sm btn-white text-white m-2 pulse-anim">
					Explore
				</router-link> -->
				<router-link to="/contact" class="btn btn-rounded btn-darkblue btn-sm m-2">Contact</router-link>
			</p>
			<div class="icon-bottom">
				<p class="text-center">
					<img src="../assets/comp/icon-git.png" height="30" class="mx-2">
					<img src="../assets/comp/icon-vue.png" height="25" class="mx-2">
					<img src="../assets/comp/icon-pwa.png" height="20" class="mx-2">
					<img src="../assets/comp/icon-firestore.png" height="28" class="mx-2">
				</p>
			</div>
		</div>
		<div class="home-comp-area">
			<div class="awan1 comp1"></div>
			<div class="awan2 comp2"></div>
			<div class="cincintitik comp3"></div>
			<div class="lingktrans comp4"></div>
			<div class="lingk3 comp5"></div>
		</div>
	</div>
</template>

<script>
import router from '@/router'
// import confetti from 'canvas-confetti'
import viewCounter from './../mixins/viewCounter'
import homeRunningText from './../mixins/homeRunningText' // biar gak panjang, tak pisah aja
import '../assets/css/home.css'
export default {
	data(){
		return {
			countdown: 6,
			is_countdown: true,
			pageview: 0
		}
	},
	props: ['is_explore_cd'],
	mixins: [viewCounter, homeRunningText],
	methods:{
		countDownTimer(){
			if(this.countdown >= 0){
				setTimeout(() => {
					this.countdown -= 1
					this.countDownTimer()
				}, 1000)
			} else {
				this.is_countdown = false
				router.push({ path: '/about' })
				// console.log('push')
			}

			if(this.countdown == 0){
				this.$emit('setExplore', false)
				// confetti({
				// 	particleCount: 100,
				// 	spread: 70,
				// 	origin: { y: 0.6 }
				// })
				// setTimeout(() => {
				// 	confetti({
				// 		angle: 45,
				// 		particleCount: 100,
				// 		spread: 70,
				// 		origin: { x: 0.1, y: 0.8 }
				// 	})
				// }, 500)
				// setTimeout(() => {
				// 	confetti({
				// 		angle: 135,
				// 		particleCount: 100,
				// 		spread: 70,
				// 		origin: { x: 0.9, y: 0.8 }
				// 	})
				// }, 800)
			}
		}
	},
	mounted(){
		this.is_countdown = this.$props.is_explore_cd
		this.updatePageview() // call mixins
		if(this.is_countdown){
			this.countDownTimer()
		}
		this.descAnimated()
	}
}
</script>